@font-face {
  font-family: 'Cormorant Italic';
  src: url('../fonts/Cormorant-Italic.eot');
  src: url('../fonts/Cormorant-Italic.eot?#iefix') format('embedded-opentype'),
  url('../fonts/Cormorant-Italic.woff') format('woff'),
  url('../fonts/Cormorant-Italic.ttf') format('truetype');
}


@font-face {
  font-family: 'Cormorant Regular';
  src: url('../fonts/Cormorant-Regular.eot');
  src: url('../fonts/Cormorant-Regular.eot?#iefix') format('embedded-opentype'),
  url('../fonts/Cormorant-Regular.woff') format('woff'),
  url('../fonts/Cormorant-Regular.ttf') format('truetype');
}


@font-face {
  font-family: 'Proxima Nova Regular';
  src: url('../fonts/ProximaNova-Regular.eot');
  src: url('../fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
  url('../fonts/ProximaNova-Regular.woff') format('woff'),
  url('../fonts/ProximaNova-Regular.ttf') format('truetype');
}
@font-face {
  font-family: 'Proxima Nova Semibold';
  src: url('../fonts/ProximaNova-Semibold.eot');
  src: url('../fonts/ProximaNova-Semibold.eot?#iefix') format('embedded-opentype'),
  url('../fonts/ProximaNova-Semibold.woff') format('woff'),
  url('../fonts/ProximaNova-Semibold.ttf') format('truetype');
}

$FontProximaNova: 'Proxima Nova Regular', Helvetica, Arial, sans-serif;
$FontProximaNovaBold: 'Proxima Nova Semibold', Helvetica, Arial, sans-serif;
$FontCormorantRegular: 'Cormorant Regular', Helvetica, Arial, sans-serif;
$FontCormorantItalic: 'Cormorant Italic', Helvetica, Arial, sans-serif;