$ColorGreen: #A9D26A;
$ColorRed: #FB607F;
$ColorBlack: #555555;
$ColorBackground: #F6F7FB;
.dev {
  position: fixed;
  bottom: 0;
  height: 150px;
  left: 0;
  width: 100%;
  transition: all 0.5s;
  padding: 50px;
  &:hover {
    height:100%;
  }
}
html, body {
  width: 100%;

  overflow-x: hidden;
  font-family: $FontCormorantRegular;
  font-size: 18px;
  background: $ColorBackground;
  color: $ColorBlack;
}

.logo {
  display: block;
  @include position(center);
  height: 45px;
  img {
    display: block;
    height: 100%;
  }
}

.header {
  padding: 10px 0;
  background: #fff;
  @include style(clearfix);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  height: 65px;
  .wrapper {
    height: 100%;
  }
}

.wrapper {
  width: 1000px;
  margin: auto;
  @include respond-to(m) {
    width: calc(100% - 20px);
  }
}

.btn {
  font-size: 14px;
  text-transform: uppercase;
  font-family: $FontProximaNovaBold;
  letter-spacing: .1em;
  border: 2px $ColorBlack solid;
  display: inline-block;
  padding: 0px 30px;
  height: 45px;
  line-height: 45px;
  cursor: pointer;
  position: relative;
  transition: all 0.5s ease;
  color: $ColorBlack;
  background: none;
  z-index: 1;
  &:before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 0%;
    height: 100%;
    background: #787878;
    z-index: -1;
    transition: all 0.2s 0.3s ease;
  }
  &:after {
    content: '';
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 0%;
    height: 100%;
    background: $ColorBlack;
    z-index: -1;
    transition: all 0.2s ease;
  }
  &.left {
    float: left;
  }
  &.right {
    float: right;
  }
  &:hover {
    color: #fff;
    &:before {
      width: 100%;
      transition: all 0.2s ease;
    }
    &:after {
      width: 100%;
      transition: all 0.2s 0.3s ease;
    }
  }
}

.popup {
  &_window {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 999;

  }
  &_mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.1);

  }
  &_content {
    @include position(center);
    transform: translate(-50%, -50%) scale(0.7);
    transition: transform 0.2s;
    background: #fff;
    padding: 30px 40px 20px 40px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
    &.active {
      transform: translate(-50%, -50%) scale(1);
    }
  }
  &_title {
    font-size: 24px;
    text-transform: uppercase;
    padding-right: 40px;
    margin-bottom: 40px;

  }
  &_close {
    @include bg-svg($close);
    position: absolute;
    top: 30px;
    right: 20px;
    cursor: pointer;
  }
  &_link {
    display: block;
    text-align: center;
    color: #555;
    cursor: pointer;
  }
}

.form {
  .form-control {
    min-width: 300px;
    width: 100%;
    height: 45px;
    border: 1px solid $ColorBlack;
    padding-left: 30px;

  }
  .form-group {
    margin-bottom: 30px;
    position: relative;
  }
  .help-block {
    color: $ColorRed;
    position: absolute;
    top: 100%;
    line-height: 20px;
    font-size: 14px;
    left: 0;
    display: none;
  }
  .error_message {
    color: #fff;
    background: $ColorRed;
    position: absolute;
    bottom: 100%;
    padding: 0 40px;
    font-size: 10px;
    text-transform: uppercase;
    left: 0;
    width: 100%;
    line-height: 30px;
    font-family: $FontProximaNovaBold;
    display: none;
  }
  .success_message {
    color: #fff;
    background: $ColorGreen;
    position: absolute;
    bottom: 100%;
    padding: 0 40px;
    font-size: 10px;
    text-transform: uppercase;
    left: 0;
    width: 100%;
    line-height: 30px;
    font-family: $FontProximaNovaBold;
    display: none;
  }
  .has-error {
    .help-block {
      display: block;
    }
    .form-control {
      border: 1px $ColorRed solid;
    }
  }
  .btn {
    margin-bottom: 20px;
  }
}

.cart {
  @include bg-svg($cart);
  float: right;
  border: 2px $ColorBlack solid;
  box-sizing: content-box;
  position: relative;
  cursor: pointer;
  margin-left: 20px;
  &:before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 0%;
    height: 100%;
    background: #787878;
    z-index: -1;
    transition: all 0.2s 0.3s ease;
  }
  &:after {
    content: '';
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 0%;
    height: 100%;
    background: $ColorBlack;
    z-index: -1;
    transition: all 0.2s ease;
  }
  &:hover {
    color: #fff;
    @include bg-svg($cartW);
    &:before {
      width: 100%;
      transition: all 0.2s ease;
    }
    &:after {
      width: 100%;
      transition: all 0.2s 0.3s ease;
    }
  }
}

.menu {
  @include bg-svg($menu);
  float: left;
  border: 2px $ColorBlack solid;
  box-sizing: content-box;
  position: relative;
  cursor: pointer;
  &:before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 0%;
    height: 100%;
    background: #787878;
    z-index: -1;
    transition: all 0.2s 0.3s ease;
  }
  &:after {
    content: '';
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 0%;
    height: 100%;
    background: $ColorBlack;
    z-index: -1;
    transition: all 0.2s ease;
  }
  &:hover {
    color: #fff;
    @include bg-svg($menuW);
    &:before {
      width: 100%;
      transition: all 0.2s ease;
    }
    &:after {
      width: 100%;
      transition: all 0.2s 0.3s ease;
    }
  }
}

.header-menu {
  width: 300px;
  height: 100vh;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 5;
  transition: all 0.3s;
  transform: translateX(-100%);
  visibility: hidden;
  &.active {
    transform: translateX(0);
    visibility: visible;
    transition: all 0.3s;
  }
  &_mask {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
  }
  &_content {
    background: #fff;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 2;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
    padding: 10px 40px;
  }
  &_title {
    font-size: 16px;
    text-transform: uppercase;
    margin-bottom: 40px;
    line-height: 45px;
    font-family: $FontProximaNovaBold;
  }
  &_close {
    @include bg-svg($close);
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
  }
}

.header-cart {
  width: 300px;
  height: 100vh;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 5;
  transition: all 0.3s;
  transform: translateX(100%);
  visibility: hidden;
  &.active {
    transform: translateX(0);
    visibility: visible;
    transition: all 0.3s;
  }
  &_mask {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
  }
  &_content {
    background: #fff;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 2;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
    padding: 10px 40px;
  }
  &_title {
    font-size: 16px;
    text-transform: uppercase;
    margin-bottom: 40px;
    line-height: 45px;
    font-family: $FontProximaNovaBold;
  }
  &_close {
    @include bg-svg($close);
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
  }
  &_empty {
    font-family: $FontCormorantItalic;
    font-size: 22px;
    color: $ColorRed;
    text-align: center;
  }
}

.content {
  padding-top: 65px;
}

.menu {
  &_ul {
  }
  &_li {
    position: relative;
  }
  &_a {
    color: $ColorBlack;
    display: block;
    line-height: 30px;
    transition: all 0.1s;
    font-size: 20px;
    &:hover {
      color: #000;
    }
  }
  &_plus {
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 30px;
    cursor: pointer;

    &:before {
      content: '';
      display: block;
      width: 1px;
      height: 13px;
      background: $ColorBlack;
      @include position(center);

    }
    &:after {
      content: '';
      display: block;
      width: 13px;
      height: 1px;
      background: $ColorBlack;
      @include position(center);
    }
    &.active {
      &:before {
        display: none;
      }
    }
  }
}

.sub-menu {
  &_ul {
    display: none;
    padding: 10px 0;
  }
  &_li {

  }
  &_a {
    color: $ColorBlack;
    display: block;
    line-height: 30px;
    padding-left: 20px;
    font-size: 16px;
    transition: all 0.1s;
    &:hover {
      color: #000;
    }
  }
}

.top-menu {
  text-align: center;
  padding: 20px 0;
  font-size: 0;
  &_ul {
    display: table;
    width: 100%;
  }
  &_li {
    display: table-cell;
  }
  &_a {
    font-size: 14px;
    font-family: $FontProximaNovaBold;
    color: #A8AFB5;
    line-height: 50px;
    display: block;
    padding: 0 20px;
    transition: all 0.2s;
    border: 1px transparent solid;

    &:hover {
      background: #fff;
      border: 1px $ColorBlack solid;
      color: $ColorBlack;
    }
  }
}